@font-face{
  font-family: "PotterFont";
  src: url('./font/HARRYP__.TTF')format("truetype");
}

.App {
  text-align: center;
  font-family: "PotterFont";
  margin: 0;
  padding:0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.player-selection {
  color: white;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}
